import React from 'react';
import Xmassy from '../images/Xmassy.png'
import Octlantis from '../images/Octlantis.png'
import BrainTraining from '../images/BrainTraining.png'
import Illuminator from '../images/Illuminator.png'
import {
  BrowserRouter as Router,
  Route,
  Link
} from "react-router-dom";
const ROUTER_BASENAME = process.env.NODE_ENV === 'development' ? '/' : '/portfolio';

function Game() {
    return(
      <div class = "page">
        <div class = "worksContentArea">
          <div  class = "contentAreaBox">

          <Link to="/contact/">
              <div class = "workContentCard">
                  <div class="workContentThmbnailWrapper">
                    ::before
                      <div class="workContentThmbnail" style = {{backgroundImage:`url(${Xmassy})`}}/>        
                  </div>
                  <div class="workContentTitle">Xmassy</div>
              </div>
            </Link>

            <Link to="/contact/">
              <div class = "workContentCard">
                  <div class="workContentThmbnailWrapper">
                    ::before
                      <div class="workContentThmbnail" style = {{backgroundImage:`url(${Octlantis})`}}/>        
                  </div>
                  <div class="workContentTitle">Octlantis</div>
              </div>
            </Link>

            <Link to="/contact/">
              <div class = "workContentCard">
                  <div class="workContentThmbnailWrapper">
                    ::before
                      <div class="workContentThmbnail" style = {{backgroundImage:`url(${BrainTraining})`}}/>        
                  </div>
                  <div class="workContentTitle">BrainTraining</div>
              </div>
            </Link>

            <Link to="/contact/">
              <div class = "workContentCard">
                  <div class="workContentThmbnailWrapper">
                    ::before
                      <div class="workContentThmbnail" style = {{backgroundImage:`url(${Illuminator})`}}/>        
                  </div>
                  <div class="workContentTitle">イルミネーター</div>
              </div>
            </Link>


          </div>
        </div>

         {/*<img src={`${process.env.PUBLIC_URL}/images/MyIcon.jpg`}/>      
        <img src={foo} />*/}
      </div>
    );  
}


export default Game;